let pageSize = 20
let _this
const JsyServer = require("@/services/jsy-server.js");
import topSearch from "@/components/topSearch.vue";
export default {
  components: {
	  topSearch
  },
  data() {
    return {
      tabOne: 0,
      //是否全部选中
      list: [{
        option: false
      }, {
        option: false
      }, {
        option: false
      }],
      inputValue: '',
      //跟进记录搜索内容
      inputValueOne: '',
      
      timeIconStatus: false,
      //时间区间选择是否被打开
      
      setOver: false,
      //返回该页是否刷新
      selectContent: [{
        name: '全部',
        id: 0
      }, {
        name: '买家',
        id: 2
      }, {
        name: '卖家',
        id: 4
      }],
      bindSelect: false,
      //是否点开搜素类别
      loading: false,
     //默认身份
	  pupDef:'',
	  //客户列表
	  customerList: [],
	  pageNum: 1,
	  isDoRefresh:false
    };
  },

  onReachBottom: function () {
	// this.pageNum = this.pageNum + 1
 //    this.getCustomerList('',this.pageNum,pageSize);

  },
  onPullDownRefresh: function () {
	if (this.pageNum < 2){
		this.pageNum == 1
	}
	this.pageNum --
    this.getCustomerList('',this.pageNum,pageSize);
    
  },
  onShow: function () {
    let pages = getCurrentPages();
    let currPage = pages[pages.length-1];
    if (currPage.data.isDoRefresh == true){
	   currPage.data.isDoRefresh = false;
	   this.getCustomerList('',this.pageNum,pageSize);
    }
    this.getCustomerList('',this.pageNum,pageSize);
    
